.mainContainer {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  box-sizing: border-box;
}
.blogSection {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  max-width: 1290px;
  text-align: center;
}

.sectionTitle {
  color: #000;
  font-size: 35px;
  font-weight: 700;
  margin-bottom: 40px;
}

.description {
  color: #5a5a5a;
  text-align: center;
  margin: 0px 0 20px 33px;
  width: 50%;
  font: 400 16px/26px Roboto, sans-serif;
}
.blogGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}

.heroSectionImage {
  background-size: cover;
  width: 100%;
}

.heroSection {
  border-radius: 25px;
  background: url('../../assets/images/backgrounds/service.svg') no-repeat;
  background-size: contain;
  background-position: center;

  display: flex;
  width: 80%;
  padding: 10vw 5vw 12vw;
  justify-content: center;
  color: white;
  box-sizing: border-box;
}

.heroContent {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  box-sizing: border-box;
}

.heroTitleContent {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.heroSubtitle {
  display: flex;
  font-weight: 300;
  font-size: 36px;
  line-height: 55px;
  margin-top: 5px;
  width: 50%;
  gap: 10px;
}

@media (max-width: 991px) {
  .blogSection {
    padding: 40px 20px;
  }
  .blogGrid {
    grid-template-columns: 1fr;
  }
  .heroSection {
    min-width: 500px;
    padding: 20px 0;
  }
  .heroTitleContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 20px;
  }
  .heroSubtitle {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 20px;
  }
}
